<template>
    <div id="CouponModal" class="modal modal-lg" tabindex="-1">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header bg-dark">
                    <h5 class="modal-title text-white">Modal title</h5>
                    <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal"
                        aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <div class="row row-cols-lg-2 row-cols-1">
                        <div class="col">
                            <label class="form-label w-100">
                                優惠券名稱
                                <input class="form-control" type="text" v-model="CouponData.title">
                            </label>
                        </div>
                        <div class="col">
                            <label class="form-label  w-100">
                                到期日
                                <input class="form-control" type="number" v-model="CouponData.due_date">
                            </label>
                        </div>
                        <div class="col">
                            <label class="form-label  w-100">
                                折扣比例: <span class="mx-2">{{CouponData.percent}}%</span>
                                <input class="d-block w-100 mt-3" type="range" step="5" v-model="CouponData.percent" min="0" max="100">
                            </label>
                        </div>
                        <div class="col">
                            <label class="form-label  w-100">
                                是否啟用
                            </label>
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="activeOrNot"
                                    id="flexRadioDefault1" value="1"  v-model="CouponData.is_enabled" checked>
                                <label class="form-check-label" for="flexRadioDefault1">
                                    是
                                </label>
                            </div>
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="activeOrNot"
                                    id="flexRadioDefault2" value="0" v-model="CouponData.is_enabled">
                                <label class="form-check-label" for="flexRadioDefault2">
                                    否
                                </label>
                            </div>
                        </div>
                        <div class="col">
                            <label class="form-label w-100">
                                測試代碼
                                <input class="form-control" type="text" v-model="CouponData.code">
                            </label>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="$emit('upDateCoupon', CouponData)">確認</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'
export default {
  data () {
    return {
      CouponModal: {},
      CouponData: {}
    }
  },
  props: ['couponDataReceived'],
  watch: {
    couponDataReceived () {
      this.CouponData = this.couponDataReceived
      if (this.CouponData.percent === undefined) {
        this.CouponData.percent = 50
      }
    }
  },
  methods: {
    modalShow () {
      this.modal.show()
    },
    modalHide () {
      this.modal.hide()
    }
  },
  mounted () {
    const CouponModal = new Modal(document.getElementById('CouponModal'))
    this.modal = CouponModal
  }
}
</script>
