<template>
  <loading v-model:active="isLoading" />
  <div class="container mt-5 mb-5">
    <button type="button" class="btn btn-primary mb-3 d-flex ms-auto" @click="openModal(true)">新增優惠券</button>
    <table class="table border">
      <thead>
        <tr>
          <th scope="col">到期日</th>
          <th scope="col">優惠券名稱</th>
          <th scope="col">是否啟用</th>
          <th scope="col">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id">
          <td>{{ item.due_date }}</td>
          <td>{{ item.title }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-primary">已啟用</span>
            <span v-else class="text-muted">未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button type="button" class="btn btn-outline-primary" @click="openModal(false, item)">編輯</button>
              <button type="button" class="btn btn-outline-danger" @click="deleteCoupon(item.id)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <pageButton :paginationReceived="pagination" @changePage="getCoupon"></pageButton>
    <CouponModal :couponDataReceived="tempCoupon" @upDateCoupon="upDateCoupon" ref="CouponModal"></CouponModal>
    <Toast ref="toast" class="position-absolute top-0 end-0"></Toast>
  </div>
</template>

<script>
// Modal
import CouponModal from '../components/CouponModal.vue'
// 讀取中樣式
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/css/index.css'
// 分頁按鈕
import pageButton from '../components/PageButton.vue'
// 吐司元件
import Toast from '../components/Toast.vue'

export default {
  data () {
    return {
      coupons: [],
      tempCoupon: {},
      pagination: {},
      isLoading: false
    }
  },
  components: {
    pageButton, Loading, CouponModal, Toast
  },
  methods: {
    getCoupon (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/coupons?page=${page}`
      this.axios.get(api).then((res) => {
        this.isLoading = false
        this.coupons = res.data.coupons
        this.pagination = res.data.pagination
      })
    },
    deleteCoupon (id) {
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/coupon/${id}`
      this.axios.delete(api).then((res) => {
        this.getCoupon()
      })
    },
    openModal (isNew, data) {
      if (isNew) {
        this.isNew = isNew
        this.tempCoupon = {}
        this.$refs.CouponModal.modalShow()
      } else if (isNew === false) {
        this.isNew = isNew
        this.tempCoupon = data
        this.$refs.CouponModal.modalShow()
      }
    },
    openToast (success, message) {
      console.log(success, message)
      this.$refs.toast.toastShow(success, message)
    },
    upDateCoupon (CouponData) {
      // 轉換字串、數字
      CouponData.percent = parseInt(CouponData.percent)
      // couponData帶入預設值
      if (CouponData.is_enabled === undefined) {
        CouponData.is_enabled = 1
      } else {
        CouponData.is_enabled = parseInt(CouponData.is_enabled)
      }

      if (this.isNew) {
        // 關閉 modal
        this.$refs.CouponModal.modalHide()
        // 製作post 路徑、參數
        const data = { data: CouponData }
        const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/coupon`
        // axios.post
        this.axios.post(api, data).then((res) => {
          // 開啟吐司元件
          this.openToast(res.data.success, res.data.message)
          this.getCoupon()
        })
      } else if (!this.isNew) {
        // 關閉 modal
        this.$refs.CouponModal.modalHide()
        // 製作post 路徑、參數
        const data = { data: CouponData }
        const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/coupon/${this.tempCoupon.id}`
        // axios.post
        this.axios.put(api, data).then((res) => {
          // 開啟吐司元件
          this.openToast(res.data.success, res.data.message)
          this.getCoupon()
        })
      }
    }
  },
  created () {
    this.getCoupon()
  }
}
</script>
